.loader {
  width: 50px;
  aspect-ratio: 1;
  border-radius: 50%;
  background:
    radial-gradient(farthest-side,#2e86de 94%,#0000) top/3px 3px no-repeat,
    conic-gradient(#0000 30%,#2e86de);
  -webkit-mask: radial-gradient(farthest-side,#0000 calc(100% - 3px),#000 0);
  animation: l13 1s infinite linear;
}

@keyframes l13{
  100%{transform: rotate(1turn)}
}

.loader2 {
  width: 50px;
  aspect-ratio: 1;
  border-radius: 50%;
  background:
    radial-gradient(farthest-side,#2e86de 94%,#0000) top/10px 10px no-repeat,
    conic-gradient(#0000 30%,#2e86de);
  -webkit-mask: radial-gradient(farthest-side,#0000 calc(100% - 10px),#000 0);
  animation: l14 1s infinite linear;
}

@keyframes l14{
  100%{transform: rotate(1turn)}
}
